@import "smart-grid.less";
.dir {
    padding: 0;
    margin: 0;
}

.reset();
body {
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}

.wrapper {
    .wrapper();
}

.banners-grup {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: stretch;
    margin: 0 -10px;
}

.banners {
    flex: 1 1 380px;
    max-width: 580px;
    min-width: 480px;
    margin: 5px 5px;
    overflow: hidden;
    background: rgba(255, 255, 255, 1);
    color: rgba(0, 0, 0, 0.9);
    border: 1px solid rgba(0, 0, 0, 0.1);
    .banner {
        position: relative;
        .banner-img {
            width: 100%;
        }
        .banner-header {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 70px;
            text-align: center;
            font-size: 24px;
            opacity: 0;
            background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(255, 255, 255, 1));
            transition-duration: 1s;
        }
    }
    .banner-text {
        display: flex;
        height: ~"calc(100% - 170px)";
        flex-direction: column;
        align-items: center;
        padding: 5px;
        h4 {
            flex: 0 1 auto;
            font-size: 20px;
            margin: 5px 0;
            color: rgba(0, 0, 0, 1);
        }
        p {
            flex: 0 1 auto;
            font-size: 18px;
            font-weight: 100;
            margin: 2px 0;
            color: rgba(0, 0, 0, 1);
        }
    }
    .banner-align {
        margin-top: auto;
    }
    .banners-ref {
        text-decoration: none;
    }
    &:hover {
        border: 1px solid rgba(0, 0, 0, 0.2);
    }
    &:hover .banner-header {
        opacity: 1;
    }
}

.info-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: stretch;
    .info {
        flex: 1 1 400px;
        margin: 5px 50px 5px 5px;
    }
    .access {
        flex: 0 1 200px;
    }
}

.grid-maps {
    .row-flex();
    justify-content: center;
    &__item {
        .col();
        .size(2);
        .size-lg(2);
        .size-md(2);
        .size-sm(4);
        .size-xs(5);
        .size-xxs(7);
        padding: 10px 5px;
    }
}

.button {
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    font-size: 18px;
    &__picture {
        overflow: hidden;
        width: 100%;
        height: 100%;
        opacity: 0.5;
    }
    &__name {
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 0;
        left: 0;
    }
    &:hover &__picture {
        opacity: 1;
    }
}

.grid-news {
    .row-flex();
    justify-content: center;
    &__item {
        .col();
        .size(4);
        .size-lg(4);
        .size-md(4);
        .size-sm(12);
        padding: 10px 5px;
    }
    &__item--border {
        border-right: 1px solid rgba(0, 0, 0, 0.4);
        border-left: 1px solid rgba(0, 0, 0, 0.5);
        .sm-block( {
            border-right: none;
            border-left: none;
        }
        )
    }
    &__date {
        font-size: 16px;
        font-weight: 700;
    }
}

.grid-actions {
    .row-flex();
    justify-content: center;
    align-items: stretch;
    &__item-first {
        .col();
        .size(7);
        .size-lg(7);
        .size-md(10);
        .size-sm(12); // padding: 10px 5px;
        background: rgba(0, 0, 0, 0.2);
        max-height: 600px;
    }
    &__item-second {
        .col();
        .size(5);
        .size-lg(5);
        .size-md(10);
        .size-sm(12); // padding: 10px 5px;
        max-height: 600px;
        background: rgba(0, 0, 0, 0.2);
    }
}

.actions {
    // position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    // align-items: stretch;
    background-color: rgb(248, 112, 0);
    // height: 100%;
    min-height: 200px;
    img {
        width: auto;
        max-width: none;
        height: 100%;
        max-height: 600px;
    }
    &__date {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        margin-left: 10px;
        margin-top: 10px;
        font-size: 24px;
    }
    &__title {
        position: absolute; // width: 100%;
        // height: 100%;
        display: flex;
        justify-content: center;
        align-items: center; // align-items: center;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        text-align: center;
        font-size: 34px;
        line-height: 34px;
        text-shadow: 1px 1px 3px black, 3px 3px 3px black, 4px 4px 5px black, 1px 1px 3px black, 1px 1px 3px black, 1px 1px 3px black; // color:rgb(248, 112, 0);
        color: rgba(255, 255, 255, 1);
        top: 0;
        left: 0;
    }
}

.wrp {
    .itm {
        // display: flex;
        // justify-content: center;
        // align-items: centr;
        width: 500px;
        height: 500px;
        background: rgba(0, 0, 0, 0.2);
        overflow: hidden;
        .pic {
            // display: flex;
            // justify-content: center;
            // align-items: centr;
            flex: 0 1 100%;
            max-width: none;
            height: 100%; // margin: 0 auto;
        }
    }
}








.action_img {
    height: 100%;
    /*width: 130%;*/
    border: none;
