.action-1 {
    position: relative;
    overflow: hidden;
    height: 450px;
    width: auto;
    margin-left: 4px;
    margin-top: 4px;
}

.action-1_fon {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
    width: 100%;
    background-color: rgba(255, 115, 0, 0.8);
}

.action-1_title {
    position: absolute;
    top: 30%;
    left: 0;
    width: auto;
    margin: 20px;
    font-weight: 400;
    line-height: 50px;
    font-size: 34px;
    color: white;
    transition-duration: 0.5s;
}

.action-1_title_fon {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 115, 0, 0);
    transition-duration: 0.5s;
}

.action-1:hover .action-1_title_fon,
.action-2:hover .action-2_title_fon,
.action-3:hover .action-3_title_fon {
    background-color: rgba(100, 100, 100, 0.3);
    transition-duration: 0.5s;
}

.action-1:hover .action-1_title,
.action-2:hover .action-2_title,
.action-3:hover .action-3_title {
    /*color: rgb(255, 115, 0);*/
    transition-duration: 0.5s;
}

