.dir {
  padding: 0;
  margin: 0;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}

.wrapper {
  max-width: 1200px;
  padding-left: 40px;
  padding-right: 40px;
  margin: 0 auto;
}

.banners-grup {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: stretch;
  margin: 0 -10px;
}

.banners {
  flex: 1 1 380px;
  max-width: 580px;
  min-width: 480px;
  margin: 5px 5px;
  overflow: hidden;
  background: #ffffff;
  color: rgba(0, 0, 0, 0.9);
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.banners .banner {
  position: relative;
}

.banners .banner .banner-img {
  width: 100%;
}

.banners .banner .banner-header {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 70px;
  text-align: center;
  font-size: 24px;
  opacity: 0;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), #ffffff);
  transition-duration: 1s;
}

.banners .banner-text {
  display: flex;
  height: calc(100% - 170px);
  flex-direction: column;
  align-items: center;
  padding: 5px;
}

.banners .banner-text h4 {
  flex: 0 1 auto;
  font-size: 20px;
  margin: 5px 0;
  color: #000000;
}

.banners .banner-text p {
  flex: 0 1 auto;
  font-size: 18px;
  font-weight: 100;
  margin: 2px 0;
  color: #000000;
}

.banners .banner-align {
  margin-top: auto;
}

.banners .banners-ref {
  text-decoration: none;
}

.banners:hover {
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.banners:hover .banner-header {
  opacity: 1;
}

.info-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: stretch;
}

.info-section .info {
  flex: 1 1 400px;
  margin: 5px 50px 5px 5px;
}

.info-section .access {
  flex: 0 1 200px;
}

.grid-maps {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
}

.grid-maps__item {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(16.666666666666668% - 10px);
  padding: 10px 5px;
}

.button {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  font-size: 18px;
}

.button__picture {
  overflow: hidden;
  width: 100%;
  height: 100%;
  opacity: 0.5;
}

.button__name {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
}

.button:hover .button__picture {
  opacity: 1;
}

.grid-news {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
}

.grid-news__item {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 10px);
  padding: 10px 5px;
}

.grid-news__item--border {
  border-right: 1px solid rgba(0, 0, 0, 0.4);
  border-left: 1px solid rgba(0, 0, 0, 0.5);
}

.grid-news__date {
  font-size: 16px;
  font-weight: 700;
}

.grid-actions {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
  align-items: stretch;
}

.grid-actions__item-first {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(58.333333333333336% - 10px);
  background: rgba(0, 0, 0, 0.2);
  max-height: 600px;
}

.grid-actions__item-second {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(41.66666666666667% - 10px);
  max-height: 600px;
  background: rgba(0, 0, 0, 0.2);
}

.actions {
  overflow: hidden;
  display: flex;
  justify-content: center;
  background-color: #f87000;
  min-height: 200px;
}

.actions img {
  width: auto;
  max-width: none;
  height: 100%;
  max-height: 600px;
}

.actions__date {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  margin-left: 10px;
  margin-top: 10px;
  font-size: 24px;
}

.actions__title {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 34px;
  line-height: 34px;
  text-shadow: 1px 1px 3px black, 3px 3px 3px black, 4px 4px 5px black, 1px 1px 3px black, 1px 1px 3px black, 1px 1px 3px black;
  color: #ffffff;
  top: 0;
  left: 0;
}

.wrp .itm {
  width: 500px;
  height: 500px;
  background: rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.wrp .itm .pic {
  flex: 0 1 100%;
  max-width: none;
  height: 100%;
}

.action_img {
  height: 100%;
  /*width: 130%;*/
  border: none;
}

.action_img--width {
  /*height: 100%;*/
  width: 100%;
  border: none;
}

.action-1 {
  position: relative;
  overflow: hidden;
  height: 450px;
  width: auto;
  margin-left: 4px;
  margin-top: 4px;
}

.action-1_fon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 115, 0, 0.8);
}

.action-1_title {
  position: absolute;
  top: 30%;
  left: 0;
  width: auto;
  margin: 20px;
  font-weight: 400;
  line-height: 50px;
  font-size: 34px;
  color: white;
  transition-duration: 0.5s;
}

.action-1_title_fon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 115, 0, 0);
  transition-duration: 0.5s;
}

.action-1:hover .action-1_title_fon,
.action-2:hover .action-2_title_fon,
.action-3:hover .action-3_title_fon {
  background-color: rgba(100, 100, 100, 0.3);
  transition-duration: 0.5s;
}

.action-1:hover .action-1_title,
.action-2:hover .action-2_title,
.action-3:hover .action-3_title {
  /*color: rgb(255, 115, 0);*/
  transition-duration: 0.5s;
}

.action-2 {
  position: relative;
  overflow: hidden;
  height: 246px;
  width: auto;
  margin-left: 4px;
  margin-top: 4px;
}

.action-2--2 {
  height: 450px;
}

.action-2--3 {
  height: 200px;
}

.action-2_fon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 115, 0, 0.8);
}

.action-2_title {
  position: absolute;
  top: 10%;
  left: 0;
  width: auto;
  margin: 20px;
  font-weight: 400;
  line-height: 30px;
  font-size: 24px;
  color: white;
}

.action-2_title_fon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 115, 0, 0.1);
  transition-duration: 0.5s;
}

.action-3 {
  position: relative;
  overflow: hidden;
  height: 200px;
  width: auto;
  margin-left: 4px;
  margin-top: 4px;
}

.action-3_fon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
  width: 100%;
  background-color: #ff7300;
}

.action-3_title {
  position: absolute;
  top: 10%;
  left: 0;
  width: auto;
  margin: 20px;
  font-weight: 400;
  line-height: 30px;
  font-size: 24px;
  color: white;
}

.action-3_title_fon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 115, 0, 0.1);
  transition-duration: 0.5s;
}

