@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 40px;
    padding-right: 40px;
  }

  .grid-maps__item {
    width: calc(16.666666666666668% - 10px);
  }

  .grid-news__item {
    width: calc(33.333333333333336% - 10px);
  }

  .grid-actions__item-first {
    width: calc(58.333333333333336% - 10px);
  }

  .grid-actions__item-second {
    width: calc(41.66666666666667% - 10px);
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 40px;
    padding-right: 40px;
  }

  .grid-maps__item {
    width: calc(16.666666666666668% - 10px);
  }

  .grid-news__item {
    width: calc(33.333333333333336% - 10px);
  }

  .grid-actions__item-first {
    width: calc(83.33333333333334% - 10px);
  }

  .grid-actions__item-second {
    width: calc(83.33333333333334% - 10px);
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .grid-maps__item {
    width: calc(33.333333333333336% - 10px);
  }

  .grid-news__item {
    width: calc(100% - 10px);
  }

  .grid-news__item--border {
    border-right: none;
    border-left: none;
  }

  .grid-actions__item-first {
    width: calc(100% - 10px);
  }

  .grid-actions__item-second {
    width: calc(100% - 10px);
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .grid-maps__item {
    width: calc(41.66666666666667% - 10px);
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .grid-maps__item {
    width: calc(58.333333333333336% - 10px);
  }
}